<template>
	<div class="finance-products-transaction">
		<div class="transaction-item">	
			<a @click="toNext('FinanceProductsOrder')" class="products-item" href="javascript:;">
				<img :src="logo">
				<span>产品预约认购</span>
			</a>
			<a @click="toNext('FinanceProductsApply')" class="products-item" href="javascript:;">
				<img :src="logo">
				<span>产品认购与申购</span>
			</a>
			<a @click="toNext('FinanceProductsRedeem')" class="products-item" href="javascript:;">
				<img :src="logo">
				<span>产品赎回</span>
			</a>
			<a @click="toNext('FinanceProductsOrderBack')" class="products-item" href="javascript:;">
				<img :src="logo">
				<span>交易撤单</span>
			</a>
		</div>
	</div>
</template>

<script>

import logo from '@/assets/img/icon-bank-logo.png';
import { MessageBox } from 'mint-ui-rem-dpr';
import service from '@/services/bank/finance-products';

export default{
	data() {
		return {
			logo: logo
		};
	},
	methods: {
		toNext(type){
			this.checkCustom(type);
		},
		checkCustom(name) {
            service.checkCustomFinance()
            .then((data) => {
                this.$router.push({name: name});
            }, (err) => {
            	var self = this;
                MessageBox({
                    title: '提示信息',
                    message: '客户未签约理财，请前往柜面签约。',
                    confirmButtonText: '返回',
                    closeOnClickModal: false
                }).then(action => {
                });
            });
        }
	}
};

</script>

<style lang="scss">
.finance-products-transaction{
	.transaction-item{
		padding-top: 50px;
		.products-item{
			display: inline-block;
			width: 30%;
			margin-bottom: 50px;
			text-align: center;
			img{
				border: 1px solid #26a2ff;
				border-radius: 5px;
				margin-bottom: 15px;
			}
			span{
				display: inline-block;
				width:100%;
				padding: 10px;
				color: #fff;
				background-color: #26a2ff;
				border-radius: 5px;
			}
		}
		.products-item:not(:nth-child(3n-1)){
			margin-left: 2%;
			margin-right: 2%;
		}
	}
}

</style>


