<template>
	<div class="finance-products-risk-assess-result">
		<mt-cell title="风险评估结果："></mt-cell>
		<mt-cell title="客户风险承受等级：" :value="detail.custrisk"></mt-cell>
		<mt-cell title="风险等级有效截止日期：" :value="detail.riskenddate"></mt-cell>
		<mt-cell class="colorRed" title="风险等级评估得分：" :value="detail.totalpoint"></mt-cell>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回首页</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/finance-products';

export default{
	data() {
		return {
			detail: {
				custrisk: '',
				riskenddate: '',
				totalpoint: ''
			}
		};
	},
	methods: {
        back() {
        	this.$router.push({name: 'FinanceProductsRisk'});
        },
        getUserRisk(depositacct) {
        	service.getUserRisk({
        		DEPOSITACCT: '6217788370100836435'
        	})
        	.then((data) => {
        		this.detail.custrisk = data.CUSTRISK;
        		this.detail.riskenddate = data.RISKENDDATE;
        		this.detail.totalpoint = data.TOTALPOINT;
        	}, (err) => {
        		console.log(err);
        	});
        }
	},
	created() {
		this.getUserRisk(this.$store.state.financeProductsRisk.search.account);
	}
};

</script>

<style lang="scss">
	
.finance-products-risk-assess-result{
	.mint-cell-title{
		width: 5rem;
	}
	.colorRed{
		.mint-cell-value{
			color: red;
		}
	}
}

</style>


